import React from 'react'
import CProduct from '../../common/cProduct/cProduct'
import "./Cart.scss"
import {useSelector} from "react-redux"
const Cart = () => {
    const data=useSelector(state=>state.cart.products)||[];
    const total=useSelector(state=>state.cart.total) || 0;
    const quantity=useSelector(state=>state.quantity)|| 0;
    console.log(data,'cart');
    const Check=async()=>{
    console.log("hello");
    }
  return <div className="Cart">
      <div className="Wrapper">
          <h1 className="Title">your bracket</h1>
          <div className="Top">
              <button className="Topbutton">Continue shopping</button>
              <div className="Toptexts">
                  <span className="toptext">shopping bag({quantity})</span>
                  <span className="toptext">your wishlist (0))</span>

              </div>
              <button className="Topbutton">Checkout now</button>

          
          </div>
          <div className="Bottom">
              <div className="Info">
                  {data.length>0&&data.map(x=>(
                  <CProduct item={x}/>

                  ))}

              </div>
              <div className="summary">
                  <h1 className="Title">
                      order summary
                  </h1>
                  <div className="summaryitem">
                      <div className="summarytext">subtotal</div>
                      <div className="summaryprice">$ {total}</div>
                  </div>
                  <div className="summaryitem">
                      <div className="summarytext">estimated shipping</div>
                      <div className="summaryprice">$ 8.00</div>
                  </div>
                  <div className="summaryitem">
                      <div className="summarytext">shipping discount</div>
                      <div className="summaryprice">$ -8.00</div>
                  </div>
                  <div className="summaryitem">
                      <span className="summarytext total">total</span>
                      <span className="summaryprice total">${total}</span>
                  </div>
                  <button className="sumarybtn" onClick={Check}>checkout now</button>
                  
              </div>
          </div>
      </div>
  </div>
}

export default Cart