import React,{useState,useEffect} from 'react'
import "./Products.scss"
import axios from "axios";
import {fake} from "./FakeData"
import ProductsItem from './ProductsItem'
const Products = ({cats,filters,sorts}) => {
  const [Products,setProducts]=useState([]);
  const [FilterProducts,setFilterProducts]=useState([]);
useEffect(()=>{
  const getProduct=async()=>{
   try {
    const {data}=await axios.get(cats?`http://localhost:3001/api/product/all?category=${cats}`:`http://localhost:3001/api/product/all?new=${true}`);
    setProducts(data)
    console.log(data,'product');
   } catch (error) {
     console.log(error.message,'error');
     
   }
  };
  getProduct()
},[])
useEffect(()=>{
  cats && setFilterProducts(
    Products.filter(item=>Object.entries(filters).every(([key,value])=>
    item[key].includes(value)))
  )
},[Products,cats,filters])
useEffect(()=>{
  console.log(sorts,'ok');
  if(sorts==='newest'){
  setFilterProducts((prev)=>[...prev].sort((a,b)=>a.createdAt - b.createdAt))
  }else if(sorts==='asc'){
    setFilterProducts((prev)=>[...prev].sort((a,b)=>a.price - b.price))
    }else{
    setFilterProducts((prev)=>[...prev].sort((a,b)=>b.price -a.price ))

    }
},[sorts])
  return <div className="Products">
{FilterProducts.length>0?FilterProducts.map(x=>(
    <ProductsItem key={x.id} item={x} />
)):Products.length>0?Products.map(x=>(
  <ProductsItem key={x.id} item={x} />
)):fake.map(x=>(
  <ProductsItem key={x.id} item={x} />
))}
  </div>
}

export default Products