import React,{useState} from 'react'
import "./ProductList.scss"
import {useParams} from "react-router-dom"
import Products from "../../common/Products/Products"
const ProductList = () => {
  const {id}=useParams();
  const [filter,setfilter]=useState({});
  const [sort,setsort]=useState('newest');

  const HandleFilter=(e)=>{
    const value=e.target.value;
    setfilter({
   ...filter,   [e.target.name]:value
    })
  }
  return <div className="ProductList">
    <h1 className="Title">{id}</h1>
      <div className="Filtercontainer">
        <div className="filter">
          <span className="filterText">filter product</span>
        <select name="color" onChange={HandleFilter} id="color">
          <option value="color" disabled >color</option>
          <option value="white">white</option>
          <option value="red">red</option>
          <option value="yellow">yellow</option>
          <option value="blue">blue</option>
          <option value="pink">pink</option>
          <option value="black">black</option>

        </select>
        <select name="size" onChange={HandleFilter} id="pro1">
          <option value="size" disabled>size</option>
          <option value="xs" >xs</option>
          <option value="s" >sm</option>
          <option value="m">m</option>
          <option value="l">l</option>
          <option value="xl">xl</option>

        </select>
        </div>
        <div className="filter">
        <span className="filterText">sort product</span>
        <select  name="sort" onChange={(e)=>{setsort(e.target.value)}} id="pro2">
          <option value="newest">Newest</option>
          <option value="asc">price (asc)</option>
          <option value="desc">price (desc)</option>
         

        </select>
        </div>
      </div>
      <Products cats={id} filters={filter} sorts={sort} />
  </div>
}

export default ProductList