import React,{useState} from 'react'
import "./Slider.scss"
import { ArrowLeftOutlined as Left, ArrowRightOutlined as Right } from "@material-ui/icons"
import W from "./m1.png"
import W2 from "./m2.png"
import W3 from "./m3.png"
import W4 from "./m4.png"
import W34 from "./e.png"

const Slider = () => {
    const [tracker,setracker]=useState(0);
    const HandleClick=(arrow)=>{
let int=arrow=='right'?tracker-100:tracker+100;
int=int<-400?0:int;
int=int>0?0:int;
setracker(int);
        

    }
    return <div className="Slider">
        <div className="Arrow" style={{display:tracker==0&&'none'}} onClick={()=>HandleClick('left')}>
            <Left />
        </div>
        <div className="Arrow" style={{display:tracker==-400&&'none'}} onClick={()=>HandleClick('right')}>
            <Right />
        </div>
        <div className="Wrapper" style={{transform:`translateX(${tracker}vw)`}} >
        <div className="Slide">
                <div className="ImgContainer">
                    <img src={W} alt='pro slider' />
                </div>
                <div className="InfoContainer">
                    <h1 className="Title">SUMMER SALE</h1>
                    <p className="Desc">DONT COMPROMISE ON STYLE! GET FLAT 30% OFF FOR NEW ARRIVALS.</p>
                    <button className="Button">SHOP NOW</button>
                </div>
            </div>
            <div className="Slide">
                <div className="ImgContainer">
                    <img src={W4} alt='pro slider' />
                </div>
                <div className="InfoContainer">
                    <h1 className="Title">WINTER SALE</h1>
                    <p className="Desc">DONT COMPROMISE ON STYLE! GET FLAT 30% OFF FOR NEW ARRIVALS.</p>
                    <button className="Button">SHOP NOW</button>
                </div>
            </div>
            <div className="Slide" >
                <div className="ImgContainer">
                    <img src={W2} alt='pro slider' />
                </div>
                <div className="InfoContainer">
                    <h1 className="Title">POPULAR SALE</h1>
                    <p className="Desc">DONT COMPROMISE ON STYLE! GET FLAT 30% OFF FOR NEW ARRIVALS.</p>
                    <button className="Button">SHOP NOW</button>
                </div>
            </div>
            <div className="Slide">
                <div className="ImgContainer">
                    <img src={W3} alt='pro slider' />
                </div>
                <div className="InfoContainer">
                    <h1 className="Title">SUMMER SALE</h1>
                    <p className="Desc">DONT COMPROMISE ON STYLE! GET FLAT 30% OFF FOR NEW ARRIVALS.</p>
                    <button className="Button">SHOP NOW</button>
                </div>
            </div>
            <div className="Slide" >
                <div className="ImgContainer">
                    <img src={W34} alt='pro slider' />
                </div>
                <div className="InfoContainer">
                    <h1 className="Title">WINTER SALE</h1>
                    <p className="Desc">DONT COMPROMISE ON STYLE! GET FLAT 30% OFF FOR NEW ARRIVALS.</p>
                    <button className="Button">SHOP NOW</button>
                </div>
            </div>
            
        </div>


    </div>
}

export default Slider