import React,{useState,useEffect} from 'react'
import "./SingleProduct.scss"
import {Remove,Add} from "@material-ui/icons"
import {useParams} from "react-router-dom"
import Loader from '../../Loader'
import {PublicRequest} from "../../Axios"
import {addProduct} from "../../../Redux/CartRedux"
import {useDispatch} from "react-redux"
const SingleProduct = () => {
    const [counter,setcounter]=useState(1);
    
    const {id}=useParams();
    const [singleProduct,setSinglePRoduct]=useState({})
    const [color,setcolor]=useState("");
    const [size,setsize]=useState("");
    const Dispatch=useDispatch()
    useEffect(()=>{
        const singlePro=async ()=>{
            const {data}=await PublicRequest.get(`/product/find/${id}`);
            setSinglePRoduct(data);
        }
            
        singlePro();
    },[id])
    const AddCart=()=>{
        console.log('hello');
        console.log(counter);
        Dispatch(
         
         addProduct({...singleProduct,quantity:counter,color,size})

            )
        

    }
    return <div className="SingleProduct">
     {singleProduct.img?<div className="Wrapperu">
         <div className="Imgcontainer">
             <img src={singleProduct.img}  alt="product avtar" />
         </div>
         <div className="Infocontainer">
             <h1 className="Title">{singleProduct.title}</h1>
             <div className="Desc">{singleProduct.desc}</div>
             <span className="price">{"$"+singleProduct.price}</span>
             <div className="filtercontainer">
                 <div className="filter">
                     <span className="filtertitle">
                         color
                     </span>
                     <div className="filtercolor">
                     {singleProduct.color.map(x=>(
                         
                     <div key={x} onClick={()=>setcolor(x)} className="filtercolorbox" style={{backgroundColor:x,color:"white"}}>
                     </div>
                     ))}</div>
                    
                 </div>
                 <div className="filter">
                     <span className="filtertitle">
                         Size
                     </span>
                  <select name="size" id="size" onChange={(e)=>setsize(e.target.value)}>
                      <option value="">select size</option>
                  {singleProduct.size.map(x=>(<option key={x} value={x} className="filtersize">
                         {x}
                     </option >))}
                    
                  </select>
                 </div>
                 
             </div>
             <div className="AddContainer">
                 <div className="amountcontainer">
                     <Remove onClick={()=>counter>1&&setcounter(counter-1)} style={{cursor:"pointer"}}/>
                     <span className="amount">{counter}</span>
                     <Add onClick={()=>setcounter(counter+1)} style={{cursor:"pointer"}}/>
                 </div>
                 <button className="addtocart" onClick={AddCart}>
                     add to cart
                 </button>
             </div>
         </div>
     </div>:<Loader/>}
  </div>
}

export default SingleProduct