import { loginFailure, loginstart, loginSuccessful } from "./UserRedux";
import {PublicRequest} from "../Component/Axios"

export const Login =async(dispatc,users)=>{
     dispatc(loginstart());
     try {
         const {user}=users;
         const check=user?'/auth/signup':`/auth/login`;
         const {data}=await PublicRequest.post(check,users);
         console.log(data,"login success");
         dispatc(loginSuccessful(data))
     } catch (error) {
         console.log(error.message);
         dispatc(loginFailure())
     }
}