import React from 'react'
import "./cProduct.scss"
import {Add,Remove} from "@material-ui/icons"
import Logo from "./7.jpg"
const cProduct = ({item}) => {
    return <>
    {item?<div className="cProduct">
    <div className="productDetails">
        <img src={item.img} alt="cart checkouts" />
        <div className="Details">

            <span className="productName pro"><b>Product: </b>{item.title}</span>
            <span className="productid pro"><b>ID: </b>{item._id}</span>
            <div className="prcolor" style={{backgroundColor:item.color}}>

            </div>
            <span className="productsize pro"><b>Size: </b> {item.size}</span>
        </div>
    </div><div className="productPriced">
  <div className="productAmountcontainer">
<Remove/>
<div className="prAmount">{item.quantity}</div>
<Add/>
  </div>
  <div className="proPrice">${parseInt(parseInt(item.quantity)*parseInt(item.price))}</div>
    </div>
</div>:<h1>no item here </h1>}

    
    </>
}

export default cProduct