import React,{useState} from 'react'
import "./Login.scss"
import {Link} from "react-router-dom"
import {Login as Pro} from "../../../Redux/ApiCall"
import {useDispatch,useSelector} from "react-redux"
const Signup = () => {
   const dispatch=useDispatch();
  const {isFetching,err}=useSelector(state=>state.user);
  const [email,setemail]=useState('');
  const [user,setuser]=useState('');
  const [cPassword,setcPassword]=useState('');

  const [password,setpassword]=useState("");
  const LoginNow=()=>{
    console.log(email,password);
    const users={email,password,user};
    Pro(dispatch,users)
    setpassword("");
    setemail("");
  }
  return <div className="Auth">
     <div className="Wrapper">
         <h1 className="Title">Create an Account</h1>
         <div className="form">
             <input type="text"value={user} onChange={(e)=>setuser(e.target.value)} placeholder='Enter Username' />
             <input type="text"value={email} onChange={(e)=>setemail(e.target.value)} placeholder='Enter Email' />
             <input type="text"value={password} onChange={(e)=>setpassword(e.target.value)} placeholder='Enter Password' />
             <input type="text"value={cPassword} onChange={(e)=>setcPassword(e.target.value)} placeholder='Confirm Password ' />
        <span className="span Agreement">by creating an account,i consent to the procession of my personal data in accordance with the <b>privacy policy</b></span>
        <div className="links">
           <Link to="/login" style={{textDecoration:"none",color:"inherit"}}>Alreay have an account?</Link>
          </div>
       <div className="create">
       <button disabled={isFetching} style={{backgroundColor:isFetching&&'black',cursor:isFetching&&'not-allowed'}} onClick={LoginNow}>Signup</button>
      {err&& 
         <div className="p" style={{color:'red'}}>something went wrong....</div>
        }
       </div>
         </div>
     </div>
  </div>
}

export default Signup