import React from 'react'
import {ShoppingCartOutlined as Cart ,SearchOutlined as Search,FavoriteBorderOutlined as Heart} from "@material-ui/icons"
import "./Productsitem.scss"
import { Link } from 'react-router-dom'
const ProductsItem = ({item}) => {
  return <div className="Productsitem">
      <img src={item.img} alt="" className="okimg" />
           <div className="info">
               <div className="icon">
                   <Cart/>
               </div>
               <div className="icon">
                   <Link to={`/product/${item._id}`}><Search/></Link>
               </div>
               <div className="icon">
                   <Heart/>
               </div>
           </div>
      <div className="Circle">
           

      </div>
 
  </div>
}

export default ProductsItem