import React from 'react'
import "./Navbar.scss"
import { Search ,Mail,ShoppingCartOutlined} from "@material-ui/icons"
import {Badge} from "@material-ui/core"
import {Link} from "react-router-dom"
import { useSelector } from 'react-redux'
const Navbar = () => {
    const cart=useSelector(state=>state.cart.quantity);
    return <div className="container">
        <div className="Wrapper">
            <div className="Left">

                <Link to="/"><span className="Language">En</span></Link>
                <div className="Search-container">
                <input type={"text"} />
                    <Search />
                   
                </div>
            </div>
            <div className="Center">
                <Link to="/"><h1 className="Logo">
                    brand
                </h1></Link>
            </div>
            <div className="Right">
               
                <Link to="/signup" ><div className="MenuItem">Register</div></Link>
                <Link to="/login" ><div className="MenuItem">Login</div></Link>
                <div className="MenuItem">
                     <Link to="/cart">
                     <Badge badgeContent={cart} color="secondary">
                    <ShoppingCartOutlined style={{color:'grey',fontSize:"25px"}}/>
                </Badge>
                     </Link>
                </div>
            </div>
        </div>
    </div>
}

export default Navbar