import React from 'react'
import HomePage from './Component/Pages/HomePage/HomePage'
import ProductList from './Component/Pages/ProductList/ProductList'
import {Routes as Switcher ,Route,Navigate} from "react-router-dom"
import Navbar from './Component/common/Navbar/Navbar'
import Announcement from './Component/common/Announcement/Announcement'
import NewsLater from './Component/NewsLater/NewsLater'
import Footer from './Component/Footer/Footer'
import SingleProduct from './Component/Pages/SingleProduct/SingleProduct'
import Signup from './Component/Pages/AuthPage/Signup'
import Login from './Component/Pages/AuthPage/Login'
import Cart from './Component/Pages/Cart/Cart'
import { useSelector } from 'react-redux'


function App() {
  const user=useSelector(state=>state.user.currntUser);

  return <>
  <Announcement/>
  <Navbar/>
  <Switcher>
    console.log(user,'exist');
<Route path="/" element={<HomePage/>}/>
<Route path="/category/:id" element={<ProductList/>}/>
<Route path="/product/:id" element={<SingleProduct/>}/>
<Route path="/signup" element={user?<Navigate to="/" />:<Signup/>}/>
<Route path="/login" element={user?<Navigate to="/" />:<Login/>}/>
<Route path="/cart" element={<Cart/>}/>







  

  </Switcher>
  <NewsLater/> 
   <Footer/>
  </>
}

export default App