import React,{useState} from 'react'
import { Link } from 'react-router-dom'
import "./Login.scss"
import {Login as Pro} from "../../../Redux/ApiCall"
import {useDispatch,useSelector} from "react-redux"
const Login = () => {
  const dispatch=useDispatch();
  const {isFetching,err}=useSelector(state=>state.user);
  const [email,setemail]=useState('');
  const [password,setpassword]=useState("");
  const LoginNow=()=>{
    console.log(email,password);
    const user={email,password};
    Pro(dispatch,user)
    setpassword("");
    setemail("");
  }
  
  return <div className="Auth">
     <div className="Wrapper">
         <h1 className="Title">Login into an Account</h1>
         <div className="form">
             <input value={email} onChange={(e)=>setemail(e.target.value)} type="text" placeholder='Enter Email' />
             <input value={password} onChange={(e)=>setpassword(e.target.value)} type="text" placeholder='Enter Password' />
             
        <span className="span Agreement">by creating an account,i consent to the procession of my personal data in accordance with the <b>privacy policy</b></span>
        <div className="links">
          <Link to="/forgot" style={{textDecoration:"none",color:"inherit"}}>forgot Password?</Link>
           <Link to="/" style={{textDecoration:"none",color:"inherit"}}>dont have an account?</Link>
          </div>
       <div className="create">
          
       <button disabled={isFetching} style={{backgroundColor:isFetching&&'black',cursor:isFetching&&'not-allowed'}} onClick={LoginNow}>Login</button>
      {err&& 
         <div className="p" style={{color:'red'}}>something went wrong....</div>
        }
       </div>
         </div>
     </div>
  </div>
}

export default Login